<template>
  <div>
    <div>
      <div class="background"></div>
      <div id="main-container" class="p-5 text-center border rounded-5 shadow bg-white">
        <h1 class="mb-5">ADCL - PROOFREADING</h1>
        <div class="excel-selector-containers">
          <div class="file-input-container">
              <p>
                Select the excel file:
              </p>
              <input id="input-file" class="form-control" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel, .xlsm" @change="readFile">
          </div>
          <div class="worksheet-selector-container">
              <p>
                Select the sheet:
              </p>
              <select :class="[workbook === '' ? 'disabled-select' : '']" :disabled="workbook === '' ? true : false" v-model="worksheet">
                <option value="" v-if="worksheet.length === 0"><p>-- select an option --</p></option>
                <option v-for="(sheetName, index) in filteredSelectableOptions" :key="index" :value="sheetName">{{ sheetName }}</option>
              </select>
          </div>
        </div>
        <div class="buttons-container">
          <button class="btn btn-primary" :class="[this.worksheet === '' ? 'disabled' : '']" @click="transformFile">PROOFREADING VALIDATION</button>
        </div>
        <div class="mistakes-container" v-if="Object.keys(mistakesFound).length != 0">
          <h3>Result of proof-reading for MSN <strong>{{ msn }}</strong> and excel tab <strong>{{ worksheet }}</strong></h3>
          <p>
            {{
              isThereAnyMistake ? 
              'The number between parentesis "()" refers to the excel row where the specific error is found.' :
              'Everything is correct, no mistake has been found.'
            }}
          </p>
          <div v-if="isThereAnyMistake">
            <div v-for="(mistakeKey, index) in Object.keys(mistakesFound)" :key="index">
              <div class="mistakes-section-container" v-if="mistakesFound[mistakeKey].length !== 0 && mistakeKey !== 'MOD'">
                <h5 class="mistakes-section-title">{{mistakesNameByKey[mistakeKey]}}:</h5>
                <div class="mistakes-list-container">
                  <span v-for="(mistake, j) in mistakesFound[mistakeKey]" :key="j">
                    ({{ mistake }})
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>


<script>
const XLSX = require("xlsx");

export default {
  name: 'ProofReading',
  data() {
    return {
      // Importing excel file data
      workbook: "",
      worksheet: "",

      // results
      mistakesFound: {},
      mistakesNameByKey: {
        MSN: "The MSN in remarks doesn't match",
        MOD: "",
        ESN_APUPN: "The engine S/N or APU P/N in remarks doesn't match",
        WO: "There should be \"whichever/which occurs first/last\" in remarks column",
        OPEN_CLOSED: "The value of the AD CLOSED column may be incorrect",
      },
      msn: "",
    }
  },
  computed: {
    filteredSelectableOptions() {
      if(this.workbook) return this.workbook.SheetNames.filter(x => !x.match("FORMAT_LAYOUT"));
      return [];
    },
    isThereAnyMistake() {
      let mistakes = false;
      Object.keys(this.mistakesFound).forEach(k => {
        if(this.mistakesFound[k].length !== 0 && k !== "MOD") mistakes = true;
      })
      return mistakes;
    }
  },
  methods: {
    readFile() {
      const input = document.querySelector("#input-file");
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        // Parse data
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        this.workbook = wb;
      }

      reader.readAsBinaryString(this.file);
    },
    transformFile() {
      // Select the excel worksheet
      const sheetName = this.worksheet;
      const sheet = this.workbook.Sheets[sheetName];

      // Transform fron excel to JSON
      const header = Array.apply(null, Array(18)).map((_,i) => `col${i}`);
      const arr = XLSX.utils.sheet_to_json(sheet, {
        header: header,
      });

      // Get the location of the table's header
      let headerIndex = 0;
      while(headerIndex < 10) {
        if(Object.values(arr[headerIndex]).includes("AD REF")) break;
        headerIndex++;
      };

      // Separete the data into header, table's header, and the data itself
      const headerData = arr.slice(0, headerIndex);
      // const tableHeaderData = arr.slice(headerIndex, headerIndex+3);
      let tableData;
      if(Object.values(arr.slice(-1)[0])[0].toLowerCase() === "end of report") { // Remove the "end of report" row (last row)
        tableData = arr.slice(headerIndex+3, -1);
      } else {
        tableData = arr.slice(headerIndex+3);
      };
      // Get all the static data to validate in the table data
      const MSN = headerData[2]["col3"];
      this.msn = MSN;
      let ESN;
      let APUPN;
      if(sheetName.toUpperCase().includes("ENG")) ESN = headerData[1]["col8"];
      if(sheetName.toUpperCase().includes("APU")) APUPN = headerData[2]["col8"];
      const woOptions = [
        "whichever occurs first",
        "whichever occurs later",
        "which occurs first",
        "whichr occurs later",
      ]; // Which / Whichever occurs options.

      // Define variable where to store the mistakes if find any
      const MSNmistakesDict = {MSN: [], MOD: [], ESN_APUPN: [], WO: [], OPEN_CLOSED: []};

      // Check for all table lines and for any proof readinf type mistake
      for(let i=0; i<tableData.length; i++) {
        const d = tableData[i];
        // Check if MSN in header matches for all MSN in REMARKS
        if(d["col13"].includes("MSN") && !d["col13"].includes("MSN is")) {
          if(MSN.includes(d["col13"].split("MSN")[1].trim().split(" ")[0].split("\n")[0].trim().replace(".", "")) === false) {
            MSNmistakesDict["MSN"].push(i+11); // +11 because the table starts at row 11
          }
        }
        // Check if MOD in APPLICABILITY matches MOD in REMARKS when not applicable
        if(d["col13"].includes("MOD") && d["col13"].toUpperCase().includes("NOT APPLICABLE")) {
          const MOD = d["col13"].split("MOD")[1].trim().split(" ")[0].replaceAll(/[.,]/g, "");
          let MODapplicabilityColum;
          let count = 0;
          do {
            MODapplicabilityColum = tableData[i-count]["col4"];
            count++;
          } while(MODapplicabilityColum === undefined);
          if(!MODapplicabilityColum.includes(MOD)) {
            MSNmistakesDict["MOD"].push(i+11);
          }
        }
        // Check if ESN in header matches ESN in REMARKS
        if(ESN) {
          if(d["col13"].includes("ESN") && !d["col13"].includes("ESN is") && d["col13"].toUpperCase().includes("NOT APPLICABLE")) {
            if(ESN.includes(d["col13"].split("ESN")[1].trim().split(" ")[0].split("\n")[0].trim().replace(".", "")) === false) {
              MSNmistakesDict["ESN_APUPN"].push(i+11); // +11 because the table starts at row 11
            }
          }
        }

        // Check if APU P/N in header matches APU P/N in REMARKS
        if(APUPN) {
          if(d["col13"].includes("P/N") && !d["col13"].includes("P/N is") && d["col13"].toUpperCase().includes("NOT APPLICABLE")) {
            if(APUPN.includes(d["col13"].split("P/N")[1].trim().split(" ")[0].split("\n")[0].trim().replace(".", "")) === false) {
              MSNmistakesDict["ESN_APUPN"].push(i+11); // +11 because the table starts at row 11
            }
          }
        }

        // Check if "whichever/which occurs first/last" is writen in remarks when 2 or 3 colulns of next due are filled
        const nextDueColumnsContent = [d["col10"], d["col11"], d["col12"]]
          .map(x => x ?
            typeof x === "string" && (x.trim() === "-" || x.trim() === "'-") ?
              false :
              true :
            false)
          .filter(x => x);
        if(nextDueColumnsContent.length > 1) {
          let isItOk = false;
          woOptions.forEach(wo => {
            if(d["col13"].toLowerCase().match(wo)) isItOk = true;
          })
          if(!isItOk) MSNmistakesDict["WO"].push(i+11);
        }

        // Check if "AD CLOSED" column has correct values: 
        //    "No" if at least one columns of next due are filled or "Remarks" has work "open".
        //    "Yes" if "Remarks" has word "closed", "not applicable" or "terminated".
        if(Object.keys(d).includes("col14")) {
          let endRowGroup = 1;
          let nextRow = tableData[i+endRowGroup];
          while(nextRow !== undefined) {
            if(!Object.keys(nextRow).includes("col14")) {
              endRowGroup++;
              nextRow = tableData[i+endRowGroup];
            } else break;
          }
          if(endRowGroup > 1) {
            let result = undefined;
            for(let j=0; j < endRowGroup; j++) {
              const currentRow = tableData[i+j];
              const nextDueColumnsContent = [currentRow["col10"], currentRow["col11"], currentRow["col12"]]
                .map(x => x ?
                  typeof x === "string" && (x.trim() === "-" || x.trim() === "'-") ?
                    false :
                    true :
                  false)
                .filter(x => x);
              if(result !== "no" && (nextDueColumnsContent.length > 0 || currentRow["col13"].toLowerCase().match("open"))) {
                result = "no";
              }
              if(result === undefined &&
              (d["col13"].toLowerCase().match("not applicable") || d["col13"].toLowerCase().match("terminated") || d["col13"].toLowerCase().match("closed"))) {
                result = "yes";
              }
            }
            if(!d["col14"].toLowerCase().match(result)) {
              const rowGroupNumbers = [];
              for(let j=0; j < endRowGroup; j++) {
                rowGroupNumbers.push(i+j+11);
              }
              MSNmistakesDict["OPEN_CLOSED"].push(rowGroupNumbers.join("/"));
            }
          } else {
            if(((nextDueColumnsContent.length > 0 || d["col13"].toLowerCase().match("open")) && d["col14"].toLowerCase().match("yes")) ||
            ((d["col13"].toLowerCase().match("not applicable") || d["col13"].toLowerCase().match("terminated") || d["col13"].toLowerCase().match("closed")) && d["col14"].toLowerCase().match("no"))) {
              MSNmistakesDict["OPEN_CLOSED"].push(i+11);
            }
          }
        }

        this.mistakesFound = MSNmistakesDict;
      };
    },
  },
  watch: {
    workbook() {
      this.worksheet = "";
      this.mistakesFound = {};
    },
    worksheet() {
      this.mistakesFound = {};
    }
  }
}
</script>


