import { createRouter, createWebHashHistory } from 'vue-router';
// import { store } from "@/store/index";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import HomeView from '../views/HomeView.vue';
import ProofReadingView from '../views/ProofReadingView.vue';
import PDFGeneratorView from '../views/PDFGeneratorView.vue';
import PDFPrinterView from '../views/PDFPrinterView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      restricted: false,
    }
  },
  {
    path: '/proof-reading',
    name: 'proof-reading',
    component: ProofReadingView,
    meta: {
      restricted: false,
    }
  },
  {
    path: '/pdf-generator',
    name: 'pdf-generator',
    component: PDFGeneratorView,
    meta: {
      restricted: false,
    }
  },
  {
    path: '/pdf-printer',
    name: 'pdf-printer',
    component: PDFPrinterView,
    meta: {
      restricted: false,
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})


