<template>
    <!-- Button trigger modal -->
    <button id="open-btn" type="button" class="hidden" data-bs-toggle="modal" data-bs-target="#loginPopup"></button>

    <!-- Modal -->
    <div class="modal fade" id="loginPopup" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="loginPopupLabel" aria-hidden="true">
    <div class="modal-dialog">
        <div class="modal-content">
        <div class="modal-header">
            <h5 class="modal-title" id="loginPopupLabel">Login</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="close"></button>
        </div>
        <div class="modal-body my-2">
            <div class="email-container input-container">
                <p>Email:</p>
                <input type="email" v-model="email">
            </div>
            <div class="password-container input-container">
                <p>Passowrd:</p>
                <input type="password" v-model="password">
            </div>
        </div>
        <div class="modal-footer">
            <button id="submit-btn" type="button" class="btn btn-primary" @click="checkCredentials">Understood</button>
        </div>
        </div>
    </div>
    </div>

</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { signInWithEmailAndPassword } from "firebase/auth";

export default {
    name: "Login",
    props: ["open", "auth"],
    emits: {
        close: null,
    },
    data() {
        return {
            email: "",
            password: "",
        }
    },
    computed: {
        ...mapGetters(["user", "login"]),
        isOpened() {
            return 22;
        }
    },
    methods: {
        ...mapMutations(["setUser", "setLogin"]),
        clickModalButton() {
            const openBtn = document.querySelector("#open-btn");
            openBtn.classList.remove("hidden");
            openBtn.click();
            openBtn.classList.add("hidden");
        },
        submit() {
            const openBtn = document.querySelector("#submit-btn");
            openBtn.click();
        },
        close() {
            if(this.open) {
                this.$emit("close");
            }
        },
        checkCredentials() {
            let check = true;
            if(this.email.length === 0) {
                check = false;
                console.log("An email is required");
            }
            if(this.password.length === 0) {
                check = false;
                console.log("A password is required");
            }
            if(check) {
                signInWithEmailAndPassword(this.auth, this.email, this.password)
                    .then((user) => {
                        this.setUser(user.user.email);
                        this.setLogin(true);
                        this.close();
                        this.clickModalButton();
                    })
                    .catch((error) => {
                        console.log(error.code)
                        if(error.code.includes("invalid-email")) {
                            console.log("This email is not valid");
                        }
                        else if(error.code.includes("missing-password")) {
                            console.log("A password is required");
                        }
                        else if(error.code.includes("user-not-found")) {
                            console.log("This email doesn't exist");
                        }
                        else if(error.code.includes("wrong-password") || error.code.includes("invalid-password")) {
                            console.log("The password is incorrect");
                        }
                    });
            }
        },
    },
    watch: {
        open(val) {
            if(val) {
                this.clickModalButton();
            }
            this.email = "";
            this.password = "";
        },
    },
    mounted() {
        let self = this;
        window.addEventListener('keypress', function(e) {
            if(e.key === "Enter" && self.open) self.submit();
        });
    },
}
</script>

<style lang="scss" scoped>
.hidden {
    display: none;
}

.input-container {
    display: flex;
    flex-direction: column;
    padding: 0 3rem;
    margin: 1rem 0;
}

.input-container > p {
    margin: 0;
}
</style>