<template>
  <div>
    <div>
      <div class="background"></div>
      <div id="main-container" class="p-5 text-center border rounded-5 shadow bg-white">
        <h1 class="mb-5">ADCL to PDF - CREATOR</h1>
        <div class="inputs-container">
          <div class="name-inputs-container">
            <div>
              <p>
                Prepared by:
              </p>
              <input type="text" v-model="prepared">
            </div>
            <div>
              <p>
                Checked by:
              </p>
              <input type="text" v-model="checked">
            </div>
          </div>
          <div class="excel-selector-containers">
            <div class="file-input-container">
                <p>
                  Select the excel file:
                </p>
                <input id="input-file" class="form-control" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel, .xlsm" @change="readFile">
            </div>
            <div class="worksheet-selector-container">
                <p>
                  Select the sheet:
                </p>
                <select :class="[workbook === '' ? 'disabled-select' : '']" :disabled="workbook === '' ? true : false" v-model="worksheet">
                  <option value="" v-if="worksheet.length === 0"><p>-- select an option --</p></option>
                  <option v-for="(sheetName, index) in workbook.SheetNames" :key="index" :value="sheetName">{{ sheetName }}</option>
                </select>
            </div>
          </div>
        </div>
        <div class="printed-parameters-container">
          <div>
            <p>
              Set the table height coefficient corrector:
            </p>
            <div>
              <button @click="() => {
                if(this.tableHeightCorrector > 0.5) this.tableHeightCorrector -= 0.01;
                this.tableHeightCorrector = Math.round(this.tableHeightCorrector*100)/100;
              }">➖</button>
              <p>{{ tableHeightCorrector }}</p>
              <button @click="() => {
                if(this.tableHeightCorrector < 1) this.tableHeightCorrector += 0.01;
                this.tableHeightCorrector = Math.round(this.tableHeightCorrector*100)/100;
              }">➕</button>
            </div>
          </div>
          <div>
            <p>
              Set the maximum value of row span in the excel file:
            </p>
            <div>
              <button @click="() => {
                if(this.maxRowsspan > 1) this.maxRowsspan--;
                this.maxRowsspan = Math.round(this.maxRowsspan);
              }">➖</button>
              <p>{{ maxRowsspan }}</p>
              <button @click="() => {
                this.maxRowsspan++;
                this.maxRowsspan = Math.round(this.maxRowsspan);
              }">➕</button>
            </div>
          </div>
          <div class="checkbox-container">
            <input id="checkbox-split" type="checkbox" v-model="splitIn2">
            <label for="checkbox-split">
              Split the document in 2
            </label>
          </div>
        </div>
        <div class="buttons-container">
          <button class="btn btn-primary" :class="[this.worksheet === '' || this.prepared === '' || this.checked === '' ? 'disabled' : '']" @click="transformFile">TRANSFORM</button>
          <button class="btn" :class="[this.html === '' ? 'disabled btn-secondary' : 'btn-success']" @click="downloadPDF">DOWNLOAD</button>
        </div>
      </div>

      <div id="test-page" class="prinet-container hidden"></div>
    </div>
    <button id="modal-btn" type="button" class="btn btn-primary hidden" data-bs-toggle="modal" data-bs-target="#msgModal"></button>

    <!-- Modal -->
    <div class="modal fade" id="msgModal" tabindex="-1" aria-labelledby="msgModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body modal-body-style">
            {{ msg }}
          </div>
        </div>
      </div>
    </div>

  </div>
</template>


<script>
const XLSX = require("xlsx");

export default {
  name: 'PDFGenerator',
  data() {
    return {
      // Importing excel file data
      workbook: "",
      worksheet: "",

      // Setups before create the pdf
      prepared: "",
      checked: "",
      maxTableHeight: 180, // In milimeters
      tableHeightCorrector: navigator.userAgent.includes("Firefox") ? 0.9 : 0.98,
      maxRowsspan: 50,
      splitIn2: false,
      msg: "",

      // PDF
      html: "",
    }
  },
  methods: {
    readFile() {
      const input = document.querySelector("#input-file");
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        // Parse data
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        this.workbook = wb;
      }

      reader.readAsBinaryString(this.file);
    },
    transformFile() {
      // Select the excel worksheet
      const sheetName = this.worksheet;
      const sheet = this.workbook.Sheets[sheetName];

      // Transform fron excel to JSON
      const header = Array.apply(null, Array(18)).map((_,i) => `col${i}`);
      const arr = XLSX.utils.sheet_to_json(sheet, {
        header: header,
      });

      // Get the location of the table's header
      let headerIndex = 0;
      while(headerIndex < 10) {
        if(Object.values(arr[headerIndex]).includes("AD REF") || Object.values(arr[headerIndex]).includes("EUAD")) break;
        headerIndex++;
      };

      // Separete the data into header, table's header, and the data itself
      const headerData = arr.slice(0, headerIndex);
      const tableHeaderData = arr.slice(headerIndex, headerIndex+3);
      let tableData;
      if(Object.values(arr.slice(-1)[0])[0].toLowerCase() === "end of report") { // Remove the "end of report" row (last row)
        tableData = arr.slice(headerIndex+3 ,-1);
      } else {
        tableData = arr.slice(headerIndex+3);
      };

      // Transform the data from the header to the needed for the PDF
      const setHeaderData = function(str, sameCell) {
        if(sameCell) {
          return headerData.map(v => {
            let result;
            Object.values(v).forEach(x => {
              if(typeof x === "string") {
                if(x.includes(str)) {
                  if(x.includes(": ")) result = x;
                  else result = x.replace(":", ": ");
                };
              }
            });
            if(result) return result;
          }).filter(x => x !== undefined)
        } else {
          return str + " " + headerData.map(v => {
            if(Object.values(v).includes(str)) {
              const index = Object.values(v).indexOf(str);
              return Object.values(v)[index+1];
            };
          }).filter(x => x !== undefined)
        }
      };

      // Create the header element in HTML
      const headerHTML = `
        <div class="first-row-header">
          <div class="img-header-container">
            <img src="Airbus_Logo_2017.svg.png">
          </div>
          <div class="title-header-container">
            <p>${setHeaderData("Airworthiness Directives", true)}</p>
          </div>
          <div class="valid-header-container">
            <p>${setHeaderData("Valid as of", true)}</p>
          </div>
        </div>
        <div class="second-row-header">
          <div>
            <p>${setHeaderData("Current Model:", false)}</p>
            <p>
              ${sheetName === "APU" ? setHeaderData("APU PN:", false) : ""}
              ${sheetName.includes("ENG") ? setHeaderData("Engine Position:", false) : ""}
            </p>
          </div>

          <div>
            <p>${setHeaderData("MSN:", false)}</p>
            <p>${sheetName === "APU" ? setHeaderData("APU SN:", false) : ""}
              ${sheetName.includes("ENG") ? setHeaderData("Engine SN:", false) : ""}
            </p>
          </div>

          <div>
            <p>${setHeaderData("A/C FH", true)} FH</p>
            <p>${sheetName === "APU" ? setHeaderData("APU TSN:", false) : ""}
              ${sheetName.includes("ENG") ? setHeaderData("Engine TSN:", false) : ""}
            </p>
          </div>

          <div>
            <p>${setHeaderData("A/C FC", true)} FC</p>
            <p>${sheetName === "APU" ? setHeaderData("APU CSN:", false) : ""}
              ${sheetName.includes("ENG") ? setHeaderData("Engine CSN:", false) : ""}
            </p>
          </div>

          <div>
            <p>${setHeaderData("EASA BW", true)}</p>
            <p>${setHeaderData("FAA BW", true)}</p>
          </div>
        </div>  
      `;

      // Set the width of all the columns of the table, in %
      const columnWidth = function(i) {
        switch(i) {
          case 0:
            return 0.050372195;  // 0.050372195;
          case 1:
            return 0.035456428;
          case 2:
            return 0.050372195;
          case 3:
            return 0.148010298;
          case 4:
            return 0.040372195;  // 0.050372195;
          case 5:
            return 0.319537695;  // 0.279537695;
          case 6:
            return 0.030372195;  // 0.050372195;
          case 7:
            return 0.030372195;  // 0.050372195;
          case 8:
            return 0.030372195;  // 0.050372195;
          case 9:
            return 0.030372195;  // 0.050372195;
          case 10:
            return 0.030372195;  // 0.050372195;
          case 11:
            return 0.030372195;  // 0.050372195;
          case 12:
            return 0.140372195;  // 0.050372195;
          case 13:
            return 0.033273633;
        }
      };

      // Set the style for the table's header
      const generalHeaderStyle = function(i=false) {
        return `
          font-size: 5px;
          word-wrap: break-word;
          border: .1px solid black;
          text-align: center;
          vertical-align: middle;
          padding: 0;
          ${i !== false ? `width: ${columnWidth(i)*270}mm` : ""};
        `;
      };
      
      // Create the table's header element in HTML. It addapts to the worksheet selected
      let tableHeaderHTML = tableHeaderData.map((v, i) => {
        const th = header.slice(1, 15).map((key, j) => {
          if(i === 0) {
            if(j < 6 || j > 11) {
              return `
                <th rowspan="3" style="
                  ${generalHeaderStyle(j)}
                  ${j === 4 || j === 13 ? "padding-left: 5px; padding-right: 5px;" : ""}
                ">
                  ${v[key]}
                </th>
              `;
            }
            if(j === 6) {
              return `
                <th colspan="6" style="
                  ${generalHeaderStyle()}
                ">
                  ${v[key]}
                </th>
              `;
            }
            return "";
          } else if(i === 1) {
            if(j === 6 || j === 9) {
              return `
                <th colspan="3" style="
                  ${generalHeaderStyle()}
                ">
                  ${v[key]}
                </th>
              `;
            }
            return "";
          } else {
            if(j >= 6 && j <= 11) {
              return `
                <th style="
                  ${generalHeaderStyle(j)}
                ">
                  ${v[key]}
                </th>
              `;
            }
            return "";
          }
        }).filter(x => x !== "").join("");
        return `
          <tr style="background: lightgrey;">
            ${th}
          </tr>
        `;
      }).join("");


      // DATA OF THE TABLE
      // Defining the style for the cells of the table that are not header
      const generalCellStyle = function(i=false, highlight=false) {
        return `
          font-size: 5px;
          border: .1px solid black;
          vertical-align: middle;
          padding: 0 5px;
          ${i !== false ? `width: ${columnWidth(i)*270}mm` : ""} !important;
          background-color: ${highlight ? "yellow" : ""};
        `;
      };

      const insideCellDivStyle = function(i=false) {
        return `
          word-wrap: break-word;
          overflow-wrap: break-word;
          text-align: ${ [0, 1, 4, 6, 7, 8, 9, 10, 11, 13].includes(i) ? "center" : ([2].includes(i) ? "start" : "justify")};
          width: 100%;
        `;
      };

      // Creating the array of all the rows styled, with the data from the excel file
      let rows = [];
      tableData.map((v, i, a) => {
        // Replacing all non displayable characters "?" to the correct ones.
        Object.keys(v).forEach(k => {
          if(v[k] && typeof v[k] === "string") {
            v[k] = v[k].replaceAll("�", "§");
            v[k] = v[k].replaceAll(/\?([A-Za-z]+)\?/g, "'$1'");
            v[k] = v[k].replaceAll(/([A-Za-z])\?([A-Za-z])/g, "$1'$2");
            v[k] = v[k].replaceAll(/(\s)\?(\s\()/g, "$1§$2");
            // v[k] = v[k].replaceAll(/(\s)\?(\s)/g, "$1-$2");
            // v[k] = v[k].replaceAll(/(\n?)\?(\s)/g, "$1-$2");
            v[k] = v[k].replaceAll(/\?/g, "-");
          }
        })
        let checkIndex;
        if(v["col5"] === undefined) checkIndex = false;
        else {
          checkIndex = i+1;
          while(checkIndex < a.length && checkIndex < (i+this.maxRowsspan)) { // Here checks the next rows to see how many rowsspan there are
            if(a[checkIndex]["col5"] !== undefined) break;
            checkIndex++;
          };
          checkIndex -= i;
        };

        let rowspanCells = []; // This variable allows to store the amount of rows that span

        let highlight = false;
        if((checkIndex !== false && (v["col17"] !== undefined && v["col17"] !== "")) || (checkIndex === false && rows.slice(-1)[0].highlight)) {
          highlight = true;
        };

        const row = header.slice(1, 15).map((key, j) => {
          if(j >= 5 && j <= 12) {let value;
            if(typeof v[key] === "number") {
              var date = String(new Date(v[key]*24*3600*1000 - 2209161600000));
              date = date.split(" ")
              value = `${date[2]}-${date[1]}-${date[3]}`
            } else {
              value = v[key] ? v[key].replaceAll("\n", "<br>") : "-";
            }
            return `
              <td style="
                  ${generalCellStyle(j, highlight)}
                ">
                <div style="${insideCellDivStyle(j)}">${value}</div>
              </td>
            `;
          }
          if(checkIndex !== false) {
            let value;
            if(j === 4 || typeof v[key] === "number") {
              var date = String(new Date(v[key]*24*3600*1000 - 2209161600000));
              date = date.split(" ")
              value = `${date[2]}-${date[1]}-${date[3]}`
            } else if(j === 1) {
              value = v[key] ? v[key].replaceAll("\n", "<br>") : "-";
            } else {
              value = v[key] ? v[key].replaceAll("\n", "<br>") : "";
            }
            rowspanCells.push(`
              <td rowspan="${checkIndex}" style="
                  ${generalCellStyle(j, highlight)}
                ">
                <div style="${insideCellDivStyle(j)}">${[0, 1].includes(j) && value.trim().length !== 0 && value.trim() !== "-" ? `${value}<br>« Cont'd »` : value}</div>
              </td>
            `);
            return `
              <td rowspan="${checkIndex}" style="
                  ${generalCellStyle(j, highlight)}
                ">
                <div style="${insideCellDivStyle(j)}">${value}</div>
              </td>
            `;
          }
        }).join("");

        // Next page scenarios. When the rowspan is sliced because of a jump to the next page
        let rowsapnCellsNextPage;
        let rowNextPage;
        if(checkIndex === false) {
          rowsapnCellsNextPage = rows.slice(-1)[0].rowspanCells.map(x => {
            return x.replace(/rowspan=\"\d+\"/, `rowspan=\"${rows.slice(-1)[0].rowspanNumber - 1}\"`);
          });
          rowNextPage = `<tr>
              ${rowsapnCellsNextPage.slice(0, -1).join("")}
              ${row}
              ${rowsapnCellsNextPage.slice(-1).join("")}
            </tr>`;
        }
        
        // Here all the data gotten from row is stored in the "rows" array
        rows.push({
          row: `<tr>${row}</tr>`,
          valid: checkIndex ? true : false,
          rowspanNumber: checkIndex ? checkIndex : (rows.slice(-1)[0].rowspanNumber - 1),
          rowspanCells: checkIndex ? rowspanCells : rowsapnCellsNextPage,
          rowNextPage: checkIndex ? row : rowNextPage,
          highlight: highlight,
        })
      });

      // This function allows to addapt the table rowspan style in case a rowspan is sliced
      const transformRowToBreakRowspanGroup = function(str) {
        let rowsForRowspanGroup = 0;
        for(let row of str.split("<tr>").reverse()) {
          rowsForRowspanGroup++;
          if(row.includes("rowspan=\"")) break;
        }
        const rowspanValues = str.match(/rowspan=\"\d+\"/g);
        const result = str.split(/rowspan=\"\d+\"/).reduce((r, v, i, a) => {
          if(i === (a.length - 1)) return r + v;
          if(i > (a.length - 8)) return r + v + `rowspan=\"${rowsForRowspanGroup}\"`;
          return r + v + rowspanValues[i];
        }, "");
        return result;
      };

      // CREATING THE PAGES
      // Here the varibles needed to create all the pages are defined. Each table in HTML will be pushed to the tables array
      const tables = [];
      let table = "";

      // Here loops for all the rows to create the pages with the needed size
      for(let row of rows) {
        if(table === "") {
          table = tableHeaderHTML + row.row;
        } else {

          // This next lines of code allows to get the height of the table for each row, and define the pages according to this heigh
          const element = document.querySelector("#test-page");
          element.classList.remove("hidden");
          element.innerHTML = `<div class="page">
            <div class="header-and-table">
              <div class="header">
                
              </div>
              <table class="test-table">
                ${table + row.row}
              </table>
            </div>          
            <div class="footer">

            </div>
          </div>`;
          const invalidHeight = document.querySelector(".test-table").offsetHeight*0.2645833333 >= 180*this.tableHeightCorrector;
          element.innerHTML = "";
          element.classList.add("hidden");

          // Once the height is more than the allowed, the page is created
          if(invalidHeight) {
            if(row.valid) {
              tables.push({
                table: table,
              });
              table = tableHeaderHTML + row.row;
            } else {
              tables.push({
                table: transformRowToBreakRowspanGroup(table),
              });
              table = tableHeaderHTML + row.rowNextPage;
            }
          } else { // If not, the table keeps adding rows
            table += row.row;
          }
        }
      }

      // Adding the end of report at the end of the table
      table = table + `<tr>
        <td colspan="14" style="font-size: 5px;
        font-weight: bold;
        text-align: center;
        margin: 0;
        padding: 0;
        border: none;">
          End of Report
        </td>
      </tr>`;
      // For the remaining rows that didn't fill all the space, they are put in a page that will be the last
      tables.push({
        table: table,
      });

      // CREATING THE LAST HTML THAT WILL BE THE PDF TO DOWNLOAD
      // The HTML with all the info and styles is created, ready to be printed
      this.html = tables.map((t, i, a) => {
        return `<div class="page ${navigator.userAgent.includes("Firefox") ? "page-firefox" : ""} ${navigator.userAgent.includes("Chrome") ? "page-chrome" : ""}">
          <div class="header-and-table">
            <div class="header">
              ${headerHTML}
            </div>
            <table class="table">
              ${t.table}
            </table>
          </div>          
          <div class="footer ${navigator.userAgent.includes("Firefox") ? "footer-firefox" : ""} ${navigator.userAgent.includes("Chrome") ? "footer-chrome" : ""}">
              <div class="first-row-footer">
                <p>Prepared by: ${this.prepared}</p>
              </div>
              <div class="second-row-footer">
                <p class="p-center">Checked by: ${this.checked}</p>
                <p class="p-end">Page ${i+1} of ${a.length}</p>
              </div>
          </div>
        </div>`
      });
      if(this.splitIn2) this.html = [this.html.slice(0, Math.round(this.html.length / 2)).join(""), this.html.slice(Math.round(this.html.length / 2)).join("")];
      else this.html = this.html.join("");
    },
    downloadPDF() {
      try {
        if(this.splitIn2) {
          this.html.forEach((html, i) => {
            setTimeout(() => {
              localStorage.setItem("html", html)
              window.open("#/pdf-printer", "_blank")
            }, 1000*i)
          })
        } else {
          localStorage.setItem("html", this.html)
          window.open("#/pdf-printer", "_blank")
        };
      } catch {
        this.showErrorMsg("The size for the PDF document is too big, please select the split it in 2 option.");
      }
    },
    showErrorMsg(msg) {
      this.msg = msg;
      const button = document.querySelector("#modal-btn");
      button.classList.remove("hidden");
      button.click();
      button.classList.add("hidden")
    }
  },
  watch: {
    readyToPrint(val) {
      setTimeout(() => {if(val) document.querySelector(".printer-container").innerHTML = this.html;}, 1000);
      // setTimeout(() => document.querySelectorAll(".table").forEach(t => console.log(t.offsetHeight*0.26458333333334 >= 180)), 2000);
    },
    worksheet() {
      this.html = "";
    },
    splitIn2() {
      this.html = "";
    },
    prepared() {
      this.html = "";
    },
    checked() {
      this.html = "";
    },
    tableHeightCorrector() {
      this.html = "";
    },
    maxRowsspan() {
      this.html = "";
    },
  }
}
</script>


<style>
.hidden {
  display: none !important;
}

.printer-container {
  margin: 0;
  width: 297mm;
  padding-left: 13.5mm;
  padding-right: 13.5mm;
  font-size: 6px;
  /* font-family: Courier, monospace; */
}

.page {
  height: 210mm;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  print-color-adjust: exact;
}

.header-and-table {
  display: flex;
  flex-direction: column;
  max-height: 200mm;
}

.header {
  height: 20mm;
  width: 100%;
}

.footer {
  height: 10mm;
  width: 100%;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.first-row-header,
.second-row-header {
  display: flex;
  /* border: 1px solid red; */
}

.first-row-header > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.first-row-header > div > p,
.second-row-header > div > p {
  margin: 0;
}

.img-header-container,
.valid-header-container {
  width: 20%;
}

.img-header-container > img {
  width: 20%;
}

.title-header-container {
  width: 60%;
}

.title-header-container > p {
  font-size: 0.8rem;
}

.second-row-header > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20%;
}

.table {
  /* border-collapse: collapse; */
  margin: 0;
  max-height: 180mm !important;
  table-layout: fixed;
}

.test-table {
  /* border-collapse: collapse; */
  margin: 0;
  max-height: 180mm;
  width: 270mm;
  table-layout: fixed;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.first-row-footer > p,
.second-row-footer > p {
  margin: 0;
  width: 100%;
  text-align: center;
}

.second-row-footer {
  display: grid;
  grid-template-columns: 20% 60% 20%;
}

.p-center {
  grid-column: 2 / 3;
}

.p-end {
  grid-column: 3 / 4;
  text-align: end !important;
}


#main-container {
  width: 1200px;
  margin: 5rem auto;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.7) !important;
}

p {
  text-align: justify;
}

.background {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(155deg, rgba(0, 0, 255, 0.6), rgba(135, 207, 235, 0.4), rgba(255, 255, 0, 0.6));
